@font-face {
  font-family: Righteous;
  font-weight: bold;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root {
  --background: rgb(245, 243, 236);
  --dark-blue: rgb(28, 44, 89);
  --darker-blue: rgb(18, 28, 57);
  --blue-green: rgb(135, 173, 171);
  --yellow: rgb(247, 185, 78);
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  background-color: var(--background);

  &::before {
    content: "";
    display: block;
    margin: auto;
    position: fixed;
    height: 100%;
    width: 100%;
    border: 20px solid transparent;
    border-image: var(--lace-border) 1 fill;
    border-image-outset: 25px;
    z-index: -1;
  }
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

#content {
  position: relative;
  padding: 0 100px;
}

.page {
  padding: 200px 0 0 0;
}

footer {
  padding: 0 100px 60px;
}
