___CSS_LOADER_EXPORT___.push([module.id, `/* Hero Section */

#hero-section {
  position: relative;
  height: 100svh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#logo {
  margin: 60px 0;
  z-index: -100;
}

ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 80px;
  z-index: -100;

  li {
    list-style: none;
    font-family: Bicyclette;
    font-weight: bold;
    font-size: 1.6rem;
    letter-spacing: 2px;
    color: var(--dark-blue);
    text-transform: uppercase;

    &:nth-of-type(3) {
      color: transparent;
      -webkit-text-stroke: 1.2px var(--dark-blue);
    }
  }
}

#down-arrow {
  position: absolute;
  top: 80%;
  cursor: pointer;
  animation: float 1s ease-in-out infinite;
  transition: 1s ease all;

  &.hidden {
    z-index: -100;
  }
}

@keyframes float {
  0% {
    transform: translatey(0px);
  }

  50% {
    transform: translatey(-10px);
  }

  100% {
    transform: translatey(0px);
  }
}

/* Menu Section */

#menu-section {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, minmax(570px, 1fr));
  gap: 30px;

  button {
    position: absolute;
    top: 80%;
    text-wrap: nowrap;
    font-family: "Nunito Sans", serif;
    text-transform: uppercase;
    font-weight: 800;
    color: var(--dark-blue);
    font-size: 0.9rem;
    border: 2px solid var(--dark-blue);
    padding: 12px 22px 10px 22px;
    background-color: transparent;
    transition: background-color 0.5s ease;

    &:hover {
      background-color: var(--yellow);
      transition: background-color 1s ease;
    }
  }
}

#menu-image {
  height: 750px;
  background-image: var(--burger-image);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -100;
}

#menu-presentation {
  height: 750px;
  border: 30px solid transparent;
  border-image: var(--menu-border) 200 stretch;
  border-image-outset: 1px;
  padding: 70px;
  z-index: -100;

  h2 {
    font-size: 2.5rem;
    text-transform: uppercase;
    font-family: Bicyclette;
    letter-spacing: 2px;
    color: var(--dark-blue);
  }

  p {
    font-family: "Nunito Sans", serif;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.6;
    color: var(--dark-blue);
    margin: 20px 0;
  }
}

/* CTA Section */

#cta-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  height: 400px;
  padding: 80px 0;

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 98px;
    width: 300px;
    color: var(--blue-green);
    font-family: Bicyclette;
    font-weight: bold;
    font-size: 1.2rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 15px;
    background-color: var(--dark-blue);
    clip-path: polygon(
      20px 0,
      calc(100% - 20px) 0,
      100% 20px,
      100% calc(100% - 20px),
      calc(100% - 20px) 100%,
      20px 100%,
      0 calc(100% - 20px),
      0 20px
    );

    &:hover {
      background-color: var(--darker-blue);
      transition: background-color 0.5s ease;
    }
  }

  p {
    text-align: center;
    font-family: "Nunito Sans", serif;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.6;
    color: var(--dark-blue);
    font-size: 1.3rem;
    padding: 0 90px;
    z-index: -100;
  }
}

#cta-list {
  display: flex;
  gap: 100px;
}

/* Happenings Section */

#happenings-section {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, minmax(570px, 1fr));
  gap: 30px;

  button {
    position: absolute;
    top: 80%;
    text-wrap: nowrap;
    font-family: "Nunito Sans", serif;
    text-transform: uppercase;
    font-weight: 800;
    color: var(--dark-blue);
    font-size: 0.9rem;
    border: 2px solid var(--dark-blue);
    padding: 12px 22px 10px 22px;
    background-color: transparent;
    transition: background-color 0.5s ease;

    &:hover {
      background-color: var(--yellow);
      transition: background-color 1s ease;
    }
  }
}

#happenings-image {
  height: 830px;
  background-image: var(--building-image);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -100;
}

#happenings-presentation {
  height: 830px;
  border: 30px solid transparent;
  border-image: var(--menu-border) 200 stretch;
  border-image-outset: 1px;
  padding: 70px;
  z-index: -100;

  h2 {
    font-size: 2.5rem;
    text-transform: uppercase;
    font-family: Bicyclette;
    letter-spacing: 2px;
    color: var(--dark-blue);
  }

  p {
    font-family: "Nunito Sans", serif;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.6;
    color: var(--dark-blue);
    margin: 20px 0;
  }
}

/* Banner Section */

#banner-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 0 20px 0;
}

#banner {
  height: 450px;
  width: 100%;
  background-image: var(--banner-image);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -100;
}

#follow {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0;
  gap: 20px;

  h2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--dark-blue);
    font-family: Bicyclette;
    font-weight: bold;
    font-size: 2.3rem;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  a {
    color: transparent;
    -webkit-text-stroke: 1.2px var(--dark-blue);
  }
}

#chevrons {
  display: flex;
  gap: 10px;
  z-index: -100;
}

/* Footer */

footer {
  display: flex;
  justify-content: space-between;
  padding: 0 0 60px 0;
}

#socials {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 70px;
  img {
    width: 30px;
    cursor: pointer;
  }
}
